import {EMouseEvent} from "../enum/e-mouse-event";
import {EKeyboardEvent} from "../enum/e-keyboard-event";
import {EInputEvent} from "../enum/e-input-event";
import {IVisualComponent} from "../interface/i-visual-component";
import {IEvent} from "../interface/i-event";
import jquery = require("jquery");
import {ABasicComponent} from "./a-basic-component";

/**
 * Classe base de todos os componentes
 *
 * @export
 * @abstract
 * @class AVisualComponent
 * @implements {IVisualComponent}
 * @memberof module:component
 */
export class AVisualComponent extends ABasicComponent implements IVisualComponent {
  
	constructor(tagh: string, tagc: string = "") {
		super(tagh,tagc);
		jquery(this.element)
			.addClass("AVisualComponent")
			.html(tagc)
			.attr("id", this.getUID());
	}

	/**
	 * Adiciona um novo listener ao componente
	 *
	 * @param {(EMouseEvent | EKeyboardEvent)} type
	 * @param {function} handler
	 */
	addEvent(type: EMouseEvent | EKeyboardEvent, handler: (eventObject?: JQueryEventObject) => any): void {
	   jquery(this.element).on(this.eventTypeToString(type), handler);       
	}

	protected eventTypeToString(type_evt: EMouseEvent | EKeyboardEvent | EInputEvent): string {
		if (type_evt < 10){
			return EInputEvent[type_evt].toLowerCase();
		} else if (type_evt < 20) {
			return EKeyboardEvent[type_evt].toLowerCase();
		}
		return EMouseEvent[type_evt].toLowerCase();
	}

	/**
	 * Remove um listener do objeto
	 *
	 * @param {(EMouseEvent|EKeyboardEvent)} type
	 * @param {Function} handler
	 */
	public removeEvent(type: EMouseEvent | EKeyboardEvent, handler?: (eventObject?: JQueryEventObject) => any): void {
		if(handler){
			jquery(this.element).off(this.eventTypeToString(type), handler);
		}else{
			jquery(this.element).off(this.eventTypeToString(type));
		}
	}

	/**
	 * Remove todos os listeners de um elemento
	 *
	 * @param {Function} [callback]
	 */
	public removeAllEvents(callback?: Function): void {
		jquery(this.element).off();
	}

	public fireEvent(type: EMouseEvent | EKeyboardEvent, ...params: any[]): IEvent {
		let evt: IEvent = (<any>jquery).Event(this.eventTypeToString(type));
		jquery(this.element).trigger(<any>evt, params);
		return evt;
	}
}
